import React, {useEffect, useRef, useState} from 'react';

function Navigation({
    hide1,
    hide2,
    hide3,
    eventmen,
    eventwomen,
    eventkids
}) {

    const el1 = useRef(null)
    const el2 = useRef(null)
    const el3 = useRef(null)
    const [color,
        setColor] = useState('light')
    const [fade,
        setFade] = useState('hide')

    useEffect(() => {
        window
            .addEventListener('load', function () {
                setTimeout(() => setFade(''), 800)
            })
    })

    return (
        <div className="offset-1 col-23 navtop justify-center">
            <div className={`col-5 col-s-21 align-center justify-between ${fade}`}>
                <a
                    ref={el1}
                    className={`navigation-link ${color} ${hide1}`}
                    onClick={eventmen}
                    href="#">Men</a>
                <a
                    ref={el2}
                    className={`navigation-link ${color} ${hide2}`}
                    onClick={eventwomen}
                    href="#">Women</a>
                <a
                    ref={el3}
                    className={`navigation-link ${color} ${hide3}`}
                    onClick={eventkids}
                    href="#">Kids</a>

            </div>
        </div>
    )
}

export default Navigation