import React, {useEffect, useState} from 'react';

function Header({bgcolor, video, title}) {

    const [fade,
        setFade] = useState('hide')

    useEffect(() => {
        window
            .addEventListener('load', function () {
                setTimeout(() => setFade(''), 800)
            })
    })

    let rari = () => {
        window.scrollTo({top: window.innerHeight, left: 100, behavior: 'smooth'});
    }

    return (
        <div className={`col-25 head ${bgcolor} align-center`}>
            <div
                className="col-25 col-s-25 video-wrapper"
                dangerouslySetInnerHTML={{
                __html: `<video class="video" autoplay loop muted playsinline> <source src=${video} alt="video" type="video/mp4"/></video>`
            }}/>
            <div
                className={`title-bloc offset-7 col-11 offset-s-1 col-s-23 flex-col ${fade}`}>
                <p className="text-overline light align-center mgbt-text-30">Illuminate your Christmas</p>
                <h1 className="big-title align-center light">{title}</h1>

                {/*<p onClick={rari} className={`light text-xs scroller`}>scroll</p>*/}
            </div>
        </div>
    )
}

export default Header