import React, {useEffect, useRef, useState} from 'react'

function Title({
    title,
    text,
    width,
    fontSize,
    marginBottom,
    align,
    button
}) {

    const [widths,
        setWidths] = useState('offset-1 col-23')
    const [marginBottoms,
        setMarginBottoms] = useState('')
    const [fontSizes,
        setFontSizes] = useState('')
    const [reveal,
        setReveal] = useState('')
    const [aligns,
        setAligns] = useState('align-start')

    const el = useRef(null)

    useEffect(() => {
        if (width === 'full') {
            setWidths('offset-1 col-23')
        }
        if (width === 'mid') {
            setWidths('offset-3 col-19')
        }

        if (fontSize === 'small') {
            setFontSizes('small-title')
        }
        if (fontSize === 'medium') {
            setFontSizes('medium-title')
        }
        if (fontSize === 'big') {
            setFontSizes('big-title')
        }

        if (marginBottom === 'none') {
            setMarginBottoms('mgbt-text-10')
        }
        if (marginBottom === 'medium') {
            setMarginBottoms('mgbt-1 mgbt-m-2')
        }
        if (marginBottom === 'big') {
            setMarginBottoms('mgbt-2 mgbt-m-4')
        }

        if (align === 'start') {
            setAligns('align-start')
        }
        if (align === 'center') {
            setAligns('align-center')
        }

        window
            .addEventListener('scroll', function () {
                const node = el.current;
                const rect = node.getBoundingClientRect();
                const dist = rect.top
                if (dist < window.innerHeight) {
                    setReveal('isvisible')
                } else {
                    setReveal('')
                }
            })

    })

    return (
        <div
            ref={el}
            className={`col-25 mgt-2 mgt-m-4 ${marginBottoms} hide ${reveal}`}>
            <div className={`${widths} offset-s-1 col-s-23 flex-col ${aligns}`}>
                <h1 className={`${fontSizes} text-l dark mgbt-text-10`}>{title}</h1>
                <p className="text-s dark">{text}</p>
                {button}
            </div>
        </div>
    )
}

export default Title